/*----------------------------------------
  General Layout Style
------------------------------------------*/
html,
body {
  height: 100%;
}

body {
  background-color: #f9f9f9;
}

@media only screen and (max-width: 600px) {
  .layout-semi-dark #header nav.navbar-color {
    background: #37474f;
  }
}

.preload-transitions * {
  transition: none !important;
}

#main {
  min-height: calc(100% - 116px);
  padding-left: 0;
  transition: 0.3s ease all;

  -webkit-backface-visibility: hidden;
  backface-visibility: hidden;
}

#main.main-full {
  padding-left: 0;
}

#main .content-wrapper-before {
  position: absolute;
  z-index: -1;
  top: 64px;
  left: 0;

  width: 100%;
  height: 185px;
  transition: 0.3s ease all;
}

footer {
  padding: 0 10px;
}

footer.page-footer {
  padding-top: 1px;
}

select {
  width: 100%;
  height: 3rem;
  padding: 5px;

  border: none;
  border-bottom: 1px solid #bdbdbd;
  border-radius: 2px;
  background-color: transparent;
}

.container {
  width: 100%;
  max-width: 100% !important;
  margin: 0 auto;
  padding: 0 0.5rem;
}

.container .dropdown-settings i.right {
  margin-left: 0;
}

@media only screen and (min-width: 601px) {
  .container {
    width: 100%;
  }
}

@media only screen and (max-width: 600px) {
  #main {
    min-height: calc(100% - 107px);
  }
}

@media only screen and (min-width: 993px) {
  .container {
    width: 100%;
  }
  .sidenav-overlay {
    background-color: transparent;
  }
}

@media only screen and (min-width: 993px) {
  #main {
    padding-left: 250px;
  }
  #main.main-full {
    padding-left: 64px;
  }
  .nav-lock ~ footer {
    padding-left: 257px;
  }
  footer {
    padding-left: 71px;
  }
}

@media only screen and (max-width: 600px) {
  #main .content-wrapper-before {
    top: 56px;
  }
}

.ps-scrollbar-x-rail {
  z-index: 9999;
}

.ps-scrollbar-y-rail {
  z-index: 9999;
}

.bg-opacity-8 {
  background-color: rgba(255, 255, 255, 0.8);
}

.height-100vh {
  height: 100vh;
}

button,
html [type="button"],
[type="reset"],
[type="submit"] {
  cursor: pointer;

  -webkit-appearance: none;
}

