.avatar-status {
  line-height: 50px;

  position: relative;

  display: inline-block;

  width: 28px;

  vertical-align: bottom;
  white-space: nowrap;

  border-radius: 50px;
}

.avatar-status i {
  position: absolute;
  right: -2px;
  bottom: 18px;

  width: 9px !important;
  height: 9px !important;

  border: 1px solid #fff;
  border-radius: 100%;
}

.avatar-status span.text-circle {
  font-size: 1.2rem;

  display: table-cell;

  text-align: center;
  vertical-align: middle;

  color: #fff;
  background: #fafafa;
}

.avatar-status img {
  width: 100%;
