/*----------------------------------------
  Material
------------------------------------------*/
@font-face {
  font-family: "Material Icons";
  font-weight: 400;
  font-style: normal;

  src: local("Material Icons"), local("MaterialIcons-Regular"),
    url(/edutek-admin/static/media/MaterialIcons-Regular.a37b0c01.ttf)
      format("truetype"),
    url(/edutek-admin/static/media/MaterialIcons-Regular.570eb838.woff2)
      format("woff2"),
    url(/edutek-admin/static/media/MaterialIcons-Regular.012cf6a1.woff)
      format("woff"),
    url(/edutek-admin/static/media/MaterialIcons-Regular.e79bfd88.eot);
}

.material-icons {
  font-family: "Material Icons";
  font-size: 24px;
  font-weight: normal;
  font-style: normal;
  line-height: 1;
  /* Preferred icon size */

  display: inline-block;

  white-space: nowrap;
  letter-spacing: normal;
  text-transform: none;
  word-wrap: normal;

  direction: ltr;
  /* Support for all WebKit browsers. */
  -webkit-font-smoothing: antialiased;
  /* Support for Safari and Chrome. */
  text-rendering: optimizeLegibility;
  /* Support for Firefox. */
  -moz-osx-font-smoothing: grayscale;
  /* Support for IE. */
  font-feature-settings: "liga";
}

/* Rules for sizing the icon. */
.material-icons.md-18 {
  font-size: 18px;
}

.material-icons.md-24 {
  font-size: 24px;
}

.material-icons.md-36 {
  font-size: 36px;
}

.material-icons.md-48 {
  font-size: 48px;
}

/* Rules for using icons as black on a light background. */
.material-icons.md-dark {
  color: rgba(0, 0, 0, 0.54);
}

.material-icons.md-dark.md-inactive {
  color: rgba(0, 0, 0, 0.26);
}

/* Rules for using icons as white on a dark background. */
.material-icons.md-light {
  color: white;
}

.material-icons.md-light.md-inactive {
  color: rgba(255, 255, 255, 0.3);
}

/*----------------------------------------
    Pulse
------------------------------------------*/
.pulse-style-one {
  display: flex;
  justify-content: space-around;
}

@media screen and (-ms-high-contrast: active), (-ms-high-contrast: none) {
  .modal {
    position: -ms-page;
    top: 10%;
  }
  .modal.bottom-sheet {
    position: -ms-page;
    top: 155%;

    height: 100%;
  }
  .modal.bottom-sheet + .modal-overlay {
    position: -ms-page;
    top: 100%;
  }
  .modal-overlay {
    position: -ms-page;
    top: 5%;

    height: 100%;
  }
}

